.header-bar {
	position: absolute;
	height: 100px;
	width: 10px;
	border: 4px solid grey;
	background-color: #222;
	background-image: url('../assets/pattern.png');	
}

.header-bar.left {
	top: -15px;
	left: 20px;
	-webkit-box-shadow: 3px 0px 10px 0px rgb(15, 15, 15);
	        box-shadow: 3px 0px 10px 0px rgb(15, 15, 15);
}

.header-bar.right {
	top: -15px;
	right: 20px;
	-webkit-box-shadow: -3px 0px 10px 0px rgb(15, 15, 15);
	        box-shadow: -3px 0px 10px 0px rgb(15, 15, 15);
}

.header-shadow {
	border: 10px solid;
	position: absolute;
	border-color: rgb(20, 20, 20) transparent transparent;
	display: block;
	width: 0px;
	height: 0px;
	z-index: -1;
}

.header-shadow.left.top {
	left: 4px;
	-webkit-transform: rotate(45deg);
	    -ms-transform: rotate(45deg);
	        transform: rotate(45deg);
}

.header-shadow.left.bottom {
	bottom: -14px;
	left: 5px;
}

.header-shadow.right.top {
	left: -14px;
	-webkit-transform: rotate(-45deg);
	    -ms-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}

.header-shadow.right.bottom {
	bottom: -14px;
	left: -15px;
}

.code-logo {
	position: relative;
	-webkit-animation: logo-spin infinite 20s linear;
	        animation: logo-spin infinite 20s linear;
	height: 20px;
	margin-right: 10px;
	top: 10px;
	left: 25px;
	float: left;
}

@-webkit-keyframes logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.header {
	position: relative;
	background-color: #222;
	background-image: url('../assets/pattern.png');
	height: 40px;
	padding: 20px;
	grid-column: 1 / -1;
	border: 4px solid grey;
	-webkit-box-shadow: 0px 0px 10px 0px black;
	        box-shadow: 0px 0px 10px 0px black;
}

.title {
	color: rgb(200, 200, 200);
	font-size: 1.5em;
	text-align: center;
	margin: 5px;
	font-family: 'MyWebFont';
}

.title:hover {
	cursor: pointer;
}



