.left-side {
	grid-column: 1 / span 1;
	height: -webkit-max-content;
	height: -moz-max-content;
	height: max-content;
	padding: 5px;
	text-align: center;
	border-right: 2px solid black;
	font-size: 15px;
}

.left-side a {
	font-family: Bookman, serif;
	font-size: 14px;
}

.left-image {
	height: 100%;
	width: 100%;
	margin-top: 5px;
}

.left-side > a:-webkit-any-link {
	text-decoration: none;
	color: #000;
}

.left-side > a:-moz-any-link {
	text-decoration: none;
	color: #000;
}

.left-side > a:any-link {
	text-decoration: none;
	color: #000;
}

.spinner {
	position: relative;
	top: 10%;
	left: 20%;
	width: 35%;
	-webkit-animation: spin infinite 8s linear;
	        animation: spin infinite 8s linear;
}

@-webkit-keyframes spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}


